import { AfterViewInit, Component, OnInit } from '@angular/core';
import { ButtonStatus } from 'src/app/enums/button-status.enum';
import { ButtonType } from 'src/app/enums/button-type.enum';
import { AlertService } from 'src/app/helpers/alert/alert.service';
import { RegisterModel } from 'src/app/models/RegisterModel/register-model';
import { VolunteerModel } from 'src/app/models/VolunteerModel/volunteer-model';
import { LanguageService } from 'src/app/services/language/language.service';
import { MailService } from 'src/app/services/mail/mail.service';
import { RegisterService } from 'src/app/services/register/register.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  
  verifyCode: number;
  confirmCode: number;
  mail: string;

  button = {
    type: ButtonType.stroked,
    status: ButtonStatus.default
  }

  constructor(
    private langService: LanguageService,
    private alertService: AlertService,
    public registerService: RegisterService
  ) { 
  }

  ngOnInit(): void {
    this.registerService.registerLevel = 1;
    this.registerService.registerModel = new RegisterModel();
    this.registerService.registerModel.volunteer = new VolunteerModel();
  }


  getKey(key: string){
    return this.langService.getKey(key);
  }


}
