import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ButtonStatus } from 'src/app/enums/button-status.enum';
import { ButtonType } from 'src/app/enums/button-type.enum';
import { AlertService } from 'src/app/helpers/alert/alert.service';
import { LanguageService } from 'src/app/services/language/language.service';
import { LoginService } from 'src/app/services/login/login.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  fotgotPasswordButton = {
    name: this.langService.getKey("Forgot Password"),
  }

  email: string;
  password: string;


  constructor(
    public langService: LanguageService,
    public loginService: LoginService,
    public alert: AlertService,
    private router: Router,
  ) { }

  ngOnInit(): void {
  }
  
  getKey(key){
    return this.langService.getKey(key);
  }

  login(){
    //login ilemleri burada olacak
    this.loginService.login(this.email,this.password);
  }

  forgotPassword(){
   this.alert.alert(this.getKey('Yapm Aamasnda!'));
  }

  register(){
    this.router.navigate(['register']);
  }
}
