import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CustomComponentsComponent } from './screens/custom-components/custom-components.component';
import { DashboardComponent } from './screens/dashboard/dashboard.component';
import { LoginComponent } from './screens/login/login.component';
import { ProjectDetailComponent } from './screens/project-detail/project-detail.component';
import { RegisterComponent } from './screens/register/register.component';
import { LoginService } from './services/login/login.service';


const routes: Routes = [
  {path:"", component:LoginComponent},
  {path:"login", component:LoginComponent},
  {path:"register", component:RegisterComponent},
  {path:"eg-components", component: CustomComponentsComponent},
  {path:"dashboard", component: DashboardComponent},
  {path:"project-detail/:id", component: ProjectDetailComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
