import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from 'src/app/helpers/alert/alert.service';
import { ProjectDetailModel } from 'src/app/models/ProjectDetailModel/project-detail-model';
import { LanguageService } from 'src/app/services/language/language.service';
import { ProjectDetailService } from 'src/app/services/project-detail/project-detail.service';

@Component({
  selector: 'app-project-detail',
  templateUrl: './project-detail.component.html',
  styleUrls: ['./project-detail.component.scss']
})
export class ProjectDetailComponent implements OnInit {

  projectId: string;
  project: ProjectDetailModel;
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private projectDetailService: ProjectDetailService,
    private langService: LanguageService,
    private alert: AlertService
  ) { }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe(params => {
      this.projectId = params.get('id');
      if(this.projectId){
        this.project = this.projectDetailService.getProject(this.projectId);
      }
    })
  }

  getKey(key){
    return this.langService.getKey(key);
  }
  apply(){
    this.alert.alert(this.getKey('Yapm Aamasnda!'));
  }
  favorite(){
    this.alert.alert(this.getKey('Yapm Aamasnda!'));
  }
  withdraw(){
    this.alert.alert(this.getKey('Yapm Aamasnda!'));
  }
  viewProfile(){
    this.alert.alert(this.getKey('Yapm Aamasnda!'));
  }
}
