import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AuthenticationService } from '../authentication/authentication.service';
import { ResponseDataModel } from 'src/app/models/ResponseDataModel/response-data-model';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  constructor(private http: HttpClient, private authService: AuthenticationService) {}

  // apiUrl: string = environment.prod.apiUrl;
  // apiUrl: string = environment.dev.apiUrl_ugur;
  apiUrl: string = environment.dev.apiUrl_fahrettin;

  getHeaders() {
    let token = this.authService.getToken();
    let header;
    if (token) {
      header = new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + token});
    } else {
      header = new HttpHeaders({ 'Content-Type': 'application/json', });
    }
    return header;
  }

  get(request, url) {
    return this.http.get<ResponseDataModel>(this.apiUrl + url, {
      headers: this.getHeaders(),
      params: request,
    });
  }

  post(request, url) {
    return this.http.post<ResponseDataModel>(this.apiUrl + url, request, {
      headers: this.getHeaders(),
    });
  }

  put(request, url) {
    return this.http.post<ResponseDataModel>(this.apiUrl + url, request, {
      headers: this.getHeaders(),
    });
  }

  delete(request, url) {
    return this.http.post<ResponseDataModel>(this.apiUrl + url,request, {
      headers: this.getHeaders(),
      params: request,
    });
  }
}
